.container form {
  margin-top: 15%;
}

.container input {
  display: block;
  padding: 10px;
  margin: 10px auto;
  font-size: 16px;
  width: 50%;
  max-width: 300px;
  box-sizing: border-box;
}

.submit {
  border: 1px solid #00000052;
  border-radius: 3px;
}

.header {
  width: 50%;
  max-width: 300px;
  box-sizing: border-box;
  font-size: 32px;
  margin: 10px auto;
}
