.container {
  position: relative;
  min-height: 100vh;
  background: #dcdcdc;
  padding: 20px;
}

.formContainer {
  margin: 10px;
}

.button {
  display: inline-block;
  position: relative;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin: 1px;
  padding: 20px;
  text-align: center;
  line-height: 20px;
  cursor: pointer;
  font-size: 20px;
  background: rgb(235, 235, 235);
  -webkit-tap-highlight-color: transparent;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

