.container {
  position: relative;
  height: 100vh;
  background: #dcdcdc;
}

.cardContainer {
  position: relative;
}

.card {
  position: relative;
  border: 1px solid #999;
  border-radius: 5px;
  margin: 10px;
  padding: 20px;
  text-align: center;
  height: 70vh;
  cursor: pointer;
  background: white;

  transition: transform 0.1s, left 0.1s;
  transform: rotate3d(0, 1, 0, 0deg);
  left: 0;
  -webkit-tap-highlight-color: transparent;
}

.card p {
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  font-size: 30px;
}

.flip {
  transform: rotate3d(0, 1, 0, 90deg);

}

.wrong {
  left: -100%;
}

.right {
  left: 100%;
}

.transition {
  transition: none;
  left: 100%;
}

.answer {
  position: absolute;
  box-sizing: border-box;
  text-align: center;
  width: 100%;
  height: 70vh;
  /* border: 1px solid #ccc; */
  /* border-radius: 5px; */
  /* z-index: 999; */
}

.answer p {
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 90px;
}

.button {
  display: inline-block;
  position: relative;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin: 5px;
  padding: 20px;
  text-align: center;
  line-height: 20px;
  cursor: pointer;
  font-size: 20px;
  background: white;
  -webkit-tap-highlight-color: transparent;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}